.navbar {
	width: 100%;
	height: 80px;
	background-color: #939393; 
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-family: Arial, sans-serif; 
  }
  
  .links {
	margin-right: 50px;
	display: flex;
	align-items: center;
  }
  
  .links a {
	text-decoration: none;
	color: white;
	margin-left: 20px;
	font-size: 18px;
	font-weight: bold; 
	text-transform: uppercase; 
  }
  